.element-overlay .grade {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}

.element-overlay {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: calc(100% - 8px);
    color: white;
    padding: 4px;
    line-height: 1.5em;
    background: rgba(0,0,0,0.5);
    -webkit-transition: opacity 0.3s ease-in-out;
    -o-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
}

.element-overlay:hover {
    opacity: 1;
}

.element-overlay .footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2em;
    line-height: 2em;
}

.element-overlay .footer .footer-content {
    text-align: center;
    cursor: pointer;
}

.element-overlay .top-right-icon {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
}


